.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1800px;
    grid-template-areas:
    "h h"
    "n c";
    grid-template-rows: 100px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-row-gap: 10px;
    grid-column-gap: 10px;
    /*grid-gap: ;*/
}

.app-wrapper-content {
    grid-area: c;
    background-color: cornflowerblue;
}